export default class HandleErrors {
  static generateErrors(errors, ...setters){
    const keys = Object.keys(errors) 
    keys.map((key, index) => setters[index](errors[key]))
  }

  static clearErrors(...setters) {
    setters.forEach(set => set(''))
  }

  static verifyForm(...params){
    return params.some(param => param.length === 0)
  }
}