import React, { useState, useEffect } from 'react'
import PubSub from 'pubsub-js'
import api from '../util/api.js'

import { Input, Select, Submit } from './FormBox'
import HandleError from '../util/HandleErrors'

function BookForm() {
  const [name, setName] = useState('')
  const [author, setAuthor] = useState('')
  const [errorName, setErrorName] = useState('')
  const [errorAuthor, setErrorAuthor] = useState('')
  const [authorList, setAuthorList] = useState([])
  const [message, setMessage] = useState('')

  useEffect(() => {
    async function loadAuthorList() {
      const response = await api.get('/authors')
      setAuthorList(response.data)
    }
    loadAuthorList()
  }, [])

  async function handleSubmit(event) {
    event.preventDefault()

    const response = await api.post('/register/books', { name, author })

    if (response.data.status === 400) {
      HandleError.generateErrors(
        response.data.error,
        setErrorName, setErrorAuthor
      )
      return
    }

    setMessage(response.data.message)
    PubSub.publish('updateBooksList', response.data.current)
    HandleError.clearErrors(setErrorName, setErrorAuthor)

    setName('')
    setAuthor('')
    document.querySelector('#name').focus()

  }

  return (
    <div className="pure-form pure-form-aligned">
      <br />
      <form className="pure-form pure-form-aligned" onSubmit={handleSubmit}>
        <Input id="name" type="text" name="name" value={name}
          set={event => setName(event.target.value)}
          error={errorName}
          label="Name"
        />
        <Select id="author" name="author" authorList={authorList}
          set={event => setAuthor(event.target.value)}
          error={errorAuthor}
          label="Author"
        />

        <Submit textButton="Save" />
      </form>
      <br />
      <span>{message}</span>
    </div>
  )
}

function BookTable(props) {
  return (
    <div>
      <table className="pure-table">
        <thead>
          <tr>
            <th>ID</th>
            <th>Name</th>
            <th>Author</th>
          </tr>
        </thead>
        <tbody>
          {
            props.books.map(book => (
              <tr key={book.id}>
                <td>{book.id}</td>
                <td>{book.name}</td>
                <td>{book.author}</td>
              </tr>
            ))
          }
        </tbody>
      </table>
    </div>
  )
}

export { BookForm, BookTable }