import React, { useState } from 'react'
import PubSub from 'pubsub-js'
import api from '../util/api.js'

import { Input, Submit } from './FormBox'
import HandleError from '../util/HandleErrors'

function AuthorForm() {
  const [name, setName] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [errorName, setErrorName] = useState('')
  const [errorEmail, setErrorEmail] = useState('')
  const [errorPassword, setErrorPassword] = useState('')
  const [message, setMessage] = useState('')

  async function handleSubmit(event) {
    event.preventDefault()

    // const verifyForm = HandleError.verifyForm(name, email, password)

    // if (verifyForm) {
    //   setMessage('Fill in all fields')
    //   return
    // }

    const response = await api.post('/register/authors', { name, email, password })

    if (response.data.status === 400) {
      HandleError.generateErrors(
        response.data.error,
        setErrorName, setErrorEmail, setErrorPassword
      )
      return
    }

    setMessage(response.data.message)
    PubSub.publish('updateAuthorsList', response.data.current)
    HandleError.clearErrors(setErrorName, setErrorEmail, setErrorPassword)

    setName('')
    setEmail('')
    setPassword('')
    document.querySelector('#name').focus()

  }

  return (
    <div className="pure-form pure-form-aligned">
      <br />
      <form className="pure-form pure-form-aligned" onSubmit={handleSubmit}>
        <Input id="name" type="text" name="name" value={name}
          set={event => setName(event.target.value)}
          error={errorName}
          label="Name"
        />
        <Input id="email" type="email" name="email" value={email}
          set={event => setEmail(event.target.value)}
          error={errorEmail}
          label="Email"
        />
        <Input id="password" type="password" name="password" value={password}
          set={event => setPassword(event.target.value)}
          error={errorPassword}
          label="Password"
        />

        <Submit textButton="Save" />
      </form>
      <br />
      <span>{message}</span>
    </div>
  )
}

function AuthorTable(props) {
  return (
    <div>
      <table className="pure-table">
        <thead>
          <tr>
            <th>ID</th>
            <th>Name</th>
            <th>Email</th>
          </tr>
        </thead>
        <tbody>
          {
            props.authors.map(author => (
              <tr key={author.id}>
                <td>{author.id}</td>
                <td>{author.name}</td>
                <td>{author.email}</td>
              </tr>
            ))
          }
        </tbody>
      </table>
    </div>
  )
}

export { AuthorForm, AuthorTable }