import React from 'react'
import { Link } from 'react-router-dom'

export default function Menu() {
  const activate = 'menu-item-divided pure-menu-selected'
  const pathName = window.location.pathname

  const menuHome = pathName === '/' ? `pure-menu-item ${activate}` : 'pure-menu-item'
  const menuAuthor = pathName === '/author' ? `pure-menu-item ${activate}` : 'pure-menu-item'
  const menuBook = pathName === '/book' ? `pure-menu-item ${activate}` : 'pure-menu-item'

  return (
    <>
      <a href="#menu" id="menuLink" className="menu-link">
        <span></span>
      </a>
      <div id="menu">
        <div className="pure-menu">
          <Link className="pure-menu-heading" to='#'>Company</Link>

          <ul className="pure-menu-list">
            <li className={menuHome}>
              <Link to='/' className="pure-menu-link">Home</Link>
            </li>
            <li className={menuAuthor}><Link to='/author' className="pure-menu-link">Author</Link></li>
            <li className={menuBook}><Link to='/book' className="pure-menu-link">Book</Link></li>
          </ul>
        </div>
      </div>
    </>
  )
}