import React from 'react'

function Input(props) {
  return (
    <div className="pure-control-group">
      <label htmlFor={props.id}>{props.label}</label>
      <input id={props.id} type={props.type} name={props.name}
        value={props.value}
        onChange={props.set}
      />&nbsp;&nbsp;
      <span className="error">{props.error}</span>
    </div>
  )
}

function Select(props) {
  return (
    <div className="pure-control-group">
      <label htmlFor={props.id}>{props.label}</label>
       <select id={props.id} name={props.name} onChange={props.set}>
         <option value=''>Select an option</option>
          {props.authorList.map(author => (
            <option key={author.id} value={author.id}>
              {author.id}: {author.name}
            </option>
          ))}
        </select>&nbsp;&nbsp;
      <span className="error">{props.error}</span>
    </div>
  )
}

function Submit(props) {
  return (
    <div className="pure-control-group">
      <label></label>
      <button type="submit" className="pure-button pure-button-primary">{props.textButton}</button>
    </div>
  )
}

export { Input, Select, Submit }